
.container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin: 10px 0;
  
}
.team-section
{
	/* background: #f1f2f6; */
	text-align: center;
}

.inner-width
{
	max-width: 1200px;
	margin: auto;
	padding: 40px;
	color: #333;
	overflow: hidden;
}

.team-section h1
{
	font-size: 20px;
	text-transform: uppercase;
	display: inline-block;
	border-bottom: 4px solid;
	padding-bottom: 10px;
}


.pe
{
	float: left;
	width: calc(100% / 3);
	overflow: hidden;
	padding: 10px 0;
	transition: 0.4s;
  	width: 100%;
}

.p-des:hover
{
color:rgb(3, 161, 200);
}
.p-name:hover
{
color: rgb(209, 29, 50);
}
.pe img
{
	width:250px;
	height: 250px;
	border-radius: 50%;
}

